.modal-vert-center {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-height: 100vh;
    overflow-y:scroll;
}



/* Masonry */



.grid-col {
  float: left;
  width: 49%;
  margin-left : 3px;
  margin-right : 2px;
}
/* 
.grid-col--4 { margin-right: 0; } */

/* hide two middle */
.grid-col--2, .grid-col--3 { display: none; }

@media ( min-width: 768px ) {
  .grid-col { width: 32%; }
  .grid-col--2 { display: block; }
}

@media ( min-width: 1200px ) {
  .grid-col { width: 23.5%; }
  .grid-col--2, .grid-col--3 { display: block; }
}

.grid-item {
  min-height: 100px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.grid-item img {
  display: block;
  width: 100%;
}
